.Blog-cards {
width: 70%;
margin: 60px auto;
display: grid;
grid-template-columns: repeat(3 , 1fr);
gap: 30px;
}
.Blog-cards .Blog-card {
box-shadow: 3px 2px 20px rgba(153, 163, 163, 0.575);
border-radius: 5px;
transition: 0.3s ease-in-out;
} 
.Blog-cards .Blog-card:hover {
box-shadow: none;
border: 1px solid rgb(150, 247, 250);

}
.Blog-cards .Blog-card .main {
width: 95%;
height: 250px;
margin: 10px 0 15px 8px;
border-radius: 7px;
object-fit: cover;
}
.Blog-cards .Blog-card .title {
display: flex;
justify-content: space-between;
align-items: center;


}
.Blog-cards .Blog-card .title .img_name {
display: flex;
align-items: center;
gap: 10px;
margin-left: 20px;
}

.Blog-cards .Blog-card .title .img-person {
width: 30px;
height: 30px;
border-radius: 50%;
object-fit: cover;
}
.Blog-cards .Blog-card .title .img_name h5 {
margin-top: 5px;
color: var(--orange-color);
font-size: 16px;


}
.Blog-cards .Blog-card .title .clender {
display: flex;
align-items: center;
gap: 15px;
margin-right: 25px;
}
.Blog-cards .Blog-card .title .clender i {
font-size: 18px;
color: var(--orange-color);


}
.Blog-cards .Blog-card .title .clender h6 {
color: var(--orange-color);
margin-top: 5px;
font-size: 20px;
}
.Blog-cards .Blog-card p {
margin: 10px 0 20px 20px;
color: var(--main-color);
font-size: 27px;
font-weight: 800;

}
.Blog-cards .Blog-card button {
border: 2px solid var(--btn-color);
margin: 0px 0 20px 20px;
background-color: #fff;
padding: 12px 18px;
border-radius: 10px;
color: var(--btn-color);
transition: 0.3s ease-in-out;
font-size: 18px;
font-weight: bold;
}
.Blog-cards .Blog-card button:hover {
    background-color: var(--btn-color);
    color: #fff;
}
.Blog-cards .Blog-card button span {
padding: 5px 10px;
margin-left: 15px;
border-radius: 8px;
color: var(--main-color);
color: #fff;
background-color: var(--btn-color);
}

@media(max-width:1290px) {
    .Blog-cards {
        width: 80%;
    }



}
@media(max-width:1120px) {
    .Blog-cards .Blog-card .title .clender {
        gap: 10px;
    }
    .Blog-cards .Blog-card .title .clender h6 {
        font-size: 15px;
    }

}
@media(max-width:1050px) {
    .Blog-cards {
        width: 90%;
    }
    .Blog-cards .Blog-card p {
        font-size: 20px;
    }
}
@media(max-width:900px) {
    .Blog-cards {
        width: 96%;
    }

}
@media(max-width:820px) {
    .Blog-cards {
     grid-template-columns: repeat(2, 1fr);
     width: 80%;
    }

}
@media(max-width:710px) {
    .Blog-cards {
        width: 90%;
    }


}
@media(max-width:580px) {
    .Blog-cards {
        grid-template-columns: repeat(1, 1fr);
        width: 65%;
       } 
}
@media(max-width:450px) {
    .Blog-cards {
        width: 85%;
    }
}